.top {
    width: 100%;
    height: 50px;
    background-color: white;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    font-family: 'Josefin Sans', sans-serif;
    z-index: 999;
    margin: 20px 0 0px;
}

.topIcon {
    font-size: 30px;
    margin-right: 10px;
    color: #444;
    cursor: pointer;
}

.topImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    cursor: pointer;
}

.topList {
    margin: 0;
    padding: 0;
    list-style: none;
}

.topListItem {
    font-family: 'Comfortaa', cursive;
    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
}

ul > li {
    display: inline-block;
}
